.bg-light {
    background-color: white !important;
}

#anytime-navlink{
    font-family: 'Ubuntu', sans-serif;
}

.anytime-dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
  }
  
  .anytime-dropdown {
    position: relative;
    display: inline-block;
  }
  
  .anytime-dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .anytime-dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .anytime-dropdown-content a:hover {background-color: #f1f1f1}

.anytime-dropdown:hover .anytime-dropdown-content {
  display: block;
}

.anytime-dropdown:hover .anytime-dropbtn {
  background-color: #3e8e41;
}